        pathological: {
          neuronLoss: 'Las neuronas son los trabajadores esenciales del cerebro: clulas especializadas que procesan informacin, envan seales y controlan todo lo que pensamos, sentimos y hacemos. En las enfermedades neurodegenerativas, estas clulas vitales comienzan a perder su funcin y a morir con el tiempo. No es un dao aleatorio; afecta de manera preferente reas responsables de la memoria, el movimiento o el pensamiento. A medida que mueren ms neuronas, fallan las funciones que controlaban, originando sntomas como la prdida de memoria en el Alzheimer o los problemas de movimiento en el Parkinson.',
          whiteMatterDisruption: 'La sustancia blanca es como la gran autopista del cerebro: una red de fibras nerviosas que transporta mensajes entre distintas regiones, igual que las carreteras conectan ciudades. Piense en ella como los cables de comunicacin que permiten que las reas del cerebro trabajen de forma coordinada. En muchos trastornos neurodegenerativos, esta red se daa: la vaina de mielina que protege las fibras puede deteriorarse o las fibras perderse. Esto interrumpe la comunicacin de alta velocidad entre regiones, contribuyendo a ralentizacin del pensamiento, problemas de coordinacin y otros sntomas cognitivos y motores.',
          proteinAggregates: 'Las protenas son como pequeas mquinas que mantienen a las clulas funcionando correctamente. En las enfermedades neurodegenerativas, a veces se pliegan de manera incorrecta y se agrupan en depsitos txicos, como si la basura se acumulara y bloqueara una carretera. Por ejemplo, en la enfermedad de Alzheimer aparecen placas de beta-amiloide y ovillos de tau; en el Parkinson se forman cuerpos de Lewy compuestos por alfa-sinuclena. Estos acmulos interfieren con la funcin normal de las neuronas y contribuyen a su muerte, y los mdicos pueden utilizarlos como pistas importantes para diagnosticar enfermedades especficas.'
        }
